.checkbox {
  color: #4a4a4a;
  font-size: 13px;
  line-height: 36px; }
  .checkbox > input {
    opacity: 0;
    position: absolute;
    pointer-events: none; }
    .checkbox > input + label {
      width: 100%;
      display: inline-block;
      cursor: pointer;
      padding-left: 45px;
      padding-right: 5px; }
      .checkbox > input + label:before {
        content: "";
        display: inline-block;
        position: absolute;
        left: 15px;
        top: 8px;
        width: 20px;
        height: 20px;
        border-radius: 3px;
        border: 1px solid #d2d5d6;
        background-color: #ffffff;
        -webkit-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s; }
      .checkbox > input + label:hover:before {
        border-color: #2196f3; }
      .checkbox > input + label:after {
        content: "";
        display: inline-block;
        position: absolute;
        left: 21px;
        top: 13px;
        width: 9px;
        height: 7px;
        opacity: 0;
        -webkit-transition: all 0.2s;
        -o-transition: all 0.2s;
        transition: all 0.2s;
        background: url("../assets/right.png"); }
    .checkbox > input:checked + label:before {
      border-color: #2196f3; }
    .checkbox > input:checked + label:after {
      top: 16px;
      opacity: 1; }



